import React, { Fragment, useEffect, useState } from "react";

//component
import importAll from "../components/Functions/ImportAll";
import { Container, Row, Col } from "react-bootstrap";

export default function SectionAboutUs3() {
  const [aboutImages, setAboutImages] = useState([]);

  useEffect(() => {
    setAboutImages(
      importAll(
        require.context("../images/about/", false, /\.jpg|\.png|\.jpeg/)
      )
    );
  }, []);
  return (
    <Fragment>
      <Container fluid className="h-50">
        <Row className="h-100 d-flex justify-content-center">
          <Col
            lg={10}
            id="about-3rd-container"
            style={{ backgroundImage: "url('" + aboutImages[1] + "')" }}
          ></Col>
        </Row>
      </Container>
    </Fragment>
  );
}
