import React, { Fragment, useEffect, useState } from "react";

//component
import { Container, Row, Col, Button } from "react-bootstrap";
import { isMobile } from "react-device-detect";
import { v4 as uuidv4 } from "uuid";
import { fetchData } from "./Functions/PageFunctions";
import CardAbout from "../components/CardsAbout";

export default function SectionAboutUs5() {
  const [dataList, setDataList] = useState([]);
  const category = "leadership";

  useEffect(() => {
    fetchData(category, "", 0, "").then((getData) => {
      const result = getData?.data?.result;
      setDataList(result);
    });
  }, []);

  return (
    <Fragment>
      <Container
        fluid
        className={`${isMobile ? "h-auto" : "h-auto"} m-0 p-0 mt-5`}
      >
        <Row className="h-auto d-flex justify-content-center w-100 m-0 p-0">
          <Col xs={12} className="text-center">
            <h1 id="about-text-title" className="fw-bold">
              Team
            </h1>
            <h1 id="about-4th-text-header" className="fw-bold">
              Our Pastor
            </h1>
          </Col>
          <Col xs={12}>
            <Row className="d-flex justify-content-center">
              <Col lg={6} xs={12} className="text-center">
                <p id="about-4th-text-body text-center">
                  Vivamus magna justo, lacinia eget consectetur sed, convallis
                  at tellus. Vestibulum ante ipsum primis in faucibus orci
                  luctus et ultrices posuere cubilia Curae
                </p>
              </Col>
            </Row>
          </Col>
          {dataList.data?.length !== 0 ? (
            dataList.data?.map((data) => {
              return (
                <Col lg={3} xs={10} md={4} key={uuidv4()}>
                  <CardAbout data={data} />
                </Col>
              );
            })
          ) : (
            <>
              {" "}
              <Col xs={12}>
                <Row className="d-flex justify-content-center">
                  <Col lg={6} xs={12} className="text-center">
                    <p className="empty-data-text-black">No Pastors set</p>
                  </Col>
                </Row>
              </Col>
            </>
          )}

          <Col xs={12} className="text-center mt-5">
            <Button size="lg" className="button-all-sermon" href="/Events">
              View Events
            </Button>
          </Col>
        </Row>
      </Container>
    </Fragment>
  );
}
