import React from "react";

//component
import { Card } from "react-bootstrap";

export default function CardsAbout({ data }) {
  return (
    <>
      <Card className="mt-3 about-card">
        <Card.Img
          variant="top"
          src={
            process.env.REACT_APP_API_URL +
            "/images/contents/" +
            data.content_thumbnail
          }
        />
        <Card.Body>
          <Card.Title className="about-card-title">
            {data.content_title}
          </Card.Title>
          <Card.Text
            dangerouslySetInnerHTML={{
              __html: data.content_description.content_description,
            }}
          ></Card.Text>
        </Card.Body>
      </Card>
    </>
  );
}
