import React, { Fragment } from "react";

// component
import { Container } from "react-bootstrap";

import AboutUs1 from "../components/SectionAboutUs1";
import AboutUs2 from "../components/SectionAboutUs2";
import AboutUs3 from "../components/SectionAboutUs3";
import AboutUs4 from "../components/SectionAboutUs4";
import AboutUs5 from "../components/SectionAboutUs5";

export default function PageAbout() {
  return (
    <Fragment>
      <AboutUs1 />
      <AboutUs2 />
      <Container fluid className="m-0 p-0 h-15"></Container>
      <AboutUs3 />
      <AboutUs4 />
      <Container className="h-15"></Container>
      <AboutUs5 />
      <Container className="h-15"></Container>
    </Fragment>
  );
}
