import React, { Fragment, useEffect, useState } from "react";

//component
import { v4 as uuidv4 } from "uuid";
import { Container, Row, Col } from "react-bootstrap";
import { isMobile } from "react-device-detect";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faFlag,
  faBible,
  faUserGroup,
} from "@fortawesome/free-solid-svg-icons";

export default function SectionAboutUs2() {
  const [about2BodyText, setAbout2BodyText] = useState([]);

  useEffect(() => {
    setAbout2BodyText([
      {
        icon: faUserGroup,
        title: "Place of Haven",
        text: "Vivamus magna justo, lacinia eget consectetur sed, convallis at tellus. Vestibulum ante ipsum primis in faucibus orci luctus et ultrices posuere cubilia Curae",
      },
      {
        icon: faFlag,
        title: "Connect Groups",
        text: "Vivamus magna justo, lacinia eget consectetur sed, convallis at tellus. Vestibulum ante ipsum primis in faucibus orci luctus et ultrices posuere cubilia Curae",
      },
      {
        icon: faBible,
        title: "Study Bible",
        text: "Vivamus magna justo, lacinia eget consectetur sed, convallis at tellus. Vestibulum ante ipsum primis in faucibus orci luctus et ultrices posuere cubilia Curae",
      },
    ]);
  }, []);
  return (
    <Fragment>
      <Container
        fluid
        className={`${isMobile ? "h-auto p-3" : "h-25"} mt-5 m-0`}
      >
        <Row className="h-100 d-flex justify-content-center">
          {about2BodyText.map((data) => {
            return (
              <Col xs={12} lg={3} key={uuidv4()}>
                <Row className="h-100">
                  <Col xs={2} className="d-flex justify-content-center p-3">
                    <FontAwesomeIcon icon={data?.icon} className="about-icon" />
                  </Col>
                  <Col xs={10}>
                    <Row>
                      <Col xs={12}>
                        <h1 className="about-2nd-headers">{data?.title}</h1>
                      </Col>
                      <Col xs={12}>
                        <p className="about-2nd-body-text">{data?.text}</p>
                      </Col>
                    </Row>
                  </Col>
                </Row>
              </Col>
            );
          })}
        </Row>
      </Container>
    </Fragment>
  );
}
