import React, { Fragment, useEffect, useState } from "react";

//component
import importAll from "../components/Functions/ImportAll";
import { Container, Row, Col, Image } from "react-bootstrap";
import { isMobile } from "react-device-detect";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { Swiper, SwiperSlide } from "swiper/react";
import { Autoplay, Pagination } from "swiper";
import { faQuoteLeftAlt } from "@fortawesome/free-solid-svg-icons";
import { v4 as uuidv4 } from "uuid";
import { fetchData } from "../components/Functions/PageFunctions";

export default function SectionAboutUs4() {
  const [about4BodyText, setAbout4BodyText] = useState([]);
  const [aboutImagesGallery, setAboutImagesGallery] = useState([]);
  const [testimonial, setTestimonial] = useState([]);

  useEffect(() => {
    fetchData("testimonial", "", 0, "").then((getData) => {
      const result = getData.data.result;
      setTestimonial(result);
    });

    setAbout4BodyText([
      {
        title: "History",
        header: "Our History",
        text: "Lorem ipsum dolor sit amet, consectetur adipiscing elit. Proin consectetur mauris vitae suscipit euismod. Aenean euluctus sem. Mauris et nibh et ante molestie consectetursed et erat. Aliquam erat volutpat. Donec a dui volutpat, vulputate lorem nec, lacinia lectus. In hac habitasse platea dictumst. Pellentesque ac ipsum ullamcorper, commodo ex porta, consequat erat.",
      },
      {
        title: "Mission",
        header: "Our Mission",
        text: "Lorem ipsum dolor sit amet, consectetur adipiscing elit. Proin consectetur mauris vitae suscipit euismod. Aenean euluctus sem. Mauris et nibh et ante molestie consectetursed et erat. Aliquam erat volutpat. Donec a dui volutpat, vulputate lorem nec, lacinia lectus. In hac habitasse platea dictumst. Pellentesque ac ipsum ullamcorper, commodo ex porta, consequat erat.",
      },
    ]);

    setAboutImagesGallery(
      importAll(
        require.context(
          "../images/about/aboutGallery",
          false,
          /\.jpg|\.png|\.jpeg/
        )
      )
    );
  }, []);
  return (
    <Fragment>
      <Container fluid className={`${isMobile ? "h-auto" : "h-100"} mt-5`}>
        <Row className="h-100">
          <Col xs={12} lg={5}>
            <Row className="h-100 d-flex justify-content-end">
              {about4BodyText.map((data, index) => {
                return (
                  <Col
                    xs={12}
                    lg={11}
                    key={uuidv4()}
                    className={`d-flex ${
                      index === 0 ? "align-items-end" : "align-items-start"
                    }`}
                  >
                    <Row>
                      <Col
                        xs={12}
                        className="d-md-flex justify-content-md-center"
                      >
                        <h1 id="about-4th-text-title" className="fw-bold">
                          {data?.title}
                        </h1>
                      </Col>
                      <Col
                        xs={12}
                        className="d-md-flex justify-content-md-center"
                      >
                        <h1 id="about-4th-text-header" className="fw-bold">
                          {data?.header}
                        </h1>
                      </Col>
                      <Col xs={12}>
                        <p id="about-4th-text-body">{data?.text}</p>
                      </Col>
                    </Row>
                  </Col>
                );
              })}
            </Row>
          </Col>
          <Col xs={12} lg={7}>
            <Row className={`${isMobile ? "h-auto" : "h-100"}`}>
              <Col xs={12} className="h-50 p-0">
                <Container
                  id="about-testimonial-image-container"
                  fluid
                  className="h-100"
                >
                  <Row className="h-100 d-flex justify-content-center">
                    <Col
                      xs={3}
                      className="p-3 mx-2 mt-5 d-flex align-items-center"
                    >
                      <Image
                        id="about-testimonial-image-1"
                        fluid
                        src={aboutImagesGallery[0]}
                      />
                    </Col>
                    <Col
                      xs={3}
                      className="p-0 mx-2 mt-5 d-flex align-items-start"
                    >
                      <Image
                        id="about-testimonial-image-2"
                        fluid
                        src={aboutImagesGallery[1]}
                      />
                    </Col>
                    <Col
                      xs={3}
                      className="p-3 mx-2 mt-5 d-flex align-items-center"
                    >
                      <Image fluid src={aboutImagesGallery[2]} />
                    </Col>
                  </Row>
                </Container>
              </Col>
              {testimonial.data?.length !== 0 ? (
                <Swiper
                  slidesPerView={1}
                  loop={testimonial.data?.length === 1 ? false : true}
                  pagination={true}
                  autoplay={{
                    delay: 7000,
                    disableOnInteraction: false,
                  }}
                  modules={[Autoplay, Pagination]}
                  className="mySwiper h-50"
                >
                  {testimonial.data?.map((data) => {
                    return (
                      <SwiperSlide key={uuidv4()}>
                        <Col
                          xs={12}
                          className="about-testimonial-bg px-5 h-100"
                        >
                          <Row className="h-100">
                            <Col xs={12} className="text-start">
                              <FontAwesomeIcon
                                icon={faQuoteLeftAlt}
                                className="about-testimonial-icon"
                              />
                              <Container
                                className="about-testimonial-container"
                                dangerouslySetInnerHTML={{
                                  __html:
                                    data?.content_description
                                      ?.content_description,
                                }}
                              ></Container>
                            </Col>
                            <Col xs={12} className="text-start">
                              <p className="about-testimonial-name fw-bold">
                                {data?.content_title}
                              </p>
                            </Col>
                          </Row>
                        </Col>
                      </SwiperSlide>
                    );
                  })}
                </Swiper>
              ) : (
                <>
                  <p className="empty-data-text-black text-center">
                    No Testimonials set
                  </p>
                </>
              )}
            </Row>
          </Col>
        </Row>
      </Container>
    </Fragment>
  );
}
